/* eslint-disable react/no-multi-comp */
import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs, { Tab } from 'react-awesome-tabs';
import Typography from '@material-ui/core/Typography';

import { Divider, Drawer } from '@material-ui/core';
import { connect } from 'react-redux'
import Grid from '@material-ui/core/Grid';

import TitleIcon from '@material-ui/icons/Title';

import { setStoryTab,setStory,deleteStoryScenePersonnage, setStoryPersonnage,addStoryPersonnage,deleteStoryPersonnage, addStoryScene, deleteStoryScene, moveStoryScene,setStoryScene,setStorySettings, addStoryDialog, moveStoryDialog, deleteStoryDialog, setStoryDialog   } from '../../actions'
import 'react-awesome-tabs/dist/react-awesome-tabs.css';
import NotesIcon from '@material-ui/icons/Notes';
import FeaturedVideoIcon from '@material-ui/icons/FeaturedVideo';
import { Text, NbPersonnages, SelectPersonnages, Dialogues, Boolean, Action, Font } from '../../components'
import { yellow } from '@material-ui/core/colors';
import Tooltip from '@material-ui/core/Tooltip';
import AutorenewIcon from '@material-ui/icons/Autorenew';
import IconButton from '@material-ui/core/IconButton';
import FormatSizeIcon from '@material-ui/icons/FormatSize';
import { Scene } from './component'
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    width: '100%',
    height: '100%',
    paddingTop: 1
  },
  
}));

export const colors = [{ color: 'black' },{ color: 'yellow' },{ color: 'red' },{ color: '#19b719' },{ color: 'hotPink' },{ color: 'blue' },{color: 'orchid' },{ color: 'darkOrange' }]


function Story(props) {

  console.log('scenes',props.story.scenes)
  props.story.scenes.map((value, index) => (console.log(value,index)))
  const classes = useStyles();
  
  const activeTab = props.activeTab;

  //const disabledcolors = [{ color: 'darkGrey' },{ color: 'lemonChiffon ' },{ color: 'd08f8f' },{ color: '#afc9b4' },{ color: 'pink' },{ color: 'lightBlue' },{ color: 'lavender' },{ color: 'moccasin' }]

  const handleTabSwitch = (active) => {
    console.log('CHANGE TAB,',active)
    props.setStoryTab(active)
  }

  const handleTabPositionChange = (a, b) => {
    if (a == 0 || b == 0) {
      return
    }
    console.log(a,b)
    const idx1 = a -1
    const idx2 = b -1

    props.moveStoryScene(idx1,idx2);

    if(activeTab == a) {
      props.setStoryTab(b)
    }else if(activeTab == b) {
      props.setStoryTab(a)      
    }

    //this.forceUpdate()
  }

  const handleTabClose = (index) => {
    console.log ('want to close')
    props.deleteStoryScene(index - 1)
    

    if(activeTab >= props.story.scenes.length) {
      props.setStoryTab(props.story.scenes.length);
    }

    //this.forceUpdate();
  }

  const handleTabAdd = () => {
    props.addStoryScene()
    props.setStoryTab(props.story.scenes.length);
  }

  const getTabs = () => {
    let tabs = [];    
    const synopsys = <div>
      <Text
        advanced
        icon={
          <Tooltip enterDelay={500} title="Reset">
            <IconButton onClick={() => props.setStory('title',"",'text')}>
              <TitleIcon/>
            </IconButton>
          </Tooltip>  
        }
        onChange={(value,key) => {props.setStory('title',value,key);}}
        text="Titre"
        value={props.story.synopsys.title}
      />
      <br/>
      <NbPersonnages
        colors = {colors}
        onAdd={props.addStoryPersonnage}
        onChange={props.setStoryPersonnage}
        onDelete={props.deleteStoryPersonnage}
        text="Nombre de personnages"
        value={props.story.synopsys.personnages}
        
      />
      <br/>
      <Font
        advanced
        icon={<FormatSizeIcon/>}
        onChange={(value,key) => {props.setStory('font',value,key);}}
        text="Police"
        value={props.story.synopsys.font}
      />
      <br/>
      <Text
        icon={
          <Tooltip enterDelay={500} title="Reset">
            <IconButton onClick={() => props.setStory('note',"",'text')}>
              <NotesIcon/>
            </IconButton>
          </Tooltip>
        }
        multiline
        onChange={(value,key) => props.setStory('note',value,key)}
        text="Note"
        value={props.story.synopsys.note}
        
      />
    </div> 
    
    const scene = activeTab > 0 &&
      <div>
        <Scene
          addDialog={(actorId,) => props.addStoryDialog(activeTab-1,actorId)}
          deleteDialog={(id) => props.deleteStoryDialog(activeTab-1,id)}
          deleteScenePersonnage={(value) => props.deleteStoryScenePersonnage(activeTab-1,value)}
          moveDialog={(id1,id2) => props.moveStoryDialog(activeTab-1,id1,id2)}
          personnages={props.story.synopsys.personnages}
          setDialog={(dialogId,key1,value,key) => props.setStoryDialog(activeTab-1,dialogId,key1,value,key)}
          setScene={(name,value,key) => props.setStoryScene(activeTab-1,name,value,key)}
          value={props.story.scenes[activeTab-1]}
          colors={colors}
        />
        <br/>
        <Action
          onClick = {() => props.setStorySettings(activeTab-1)}
          text = "Importer les paramètres photo actuel"
          icon={<FormatSizeIcon/>}
          buttonText = "importer"
        />
      </div>
    
    tabs.push({title: 'Synopsys',content : synopsys,delete: false});
    props.story.scenes.map((value, index) => {
      const close = (props.story.scenes.length > 1) ? true : false ;
      tabs.push({title: 'Scene ' + (index + 1).toString(), content: scene,close: close})
    })

    return(tabs);
    
  }

  console.log('CurrentScene',activeTab-1)

  return (
    <div className={classes.root}>      
      <Tabs
        active={activeTab}
        draggable
        onTabAdd={handleTabAdd}
        onTabClose={handleTabClose}
        onTabPositionChange={handleTabPositionChange}
        onTabSwitch={handleTabSwitch}
        showAdd
      > 
        {
          getTabs().map((value, index) => {
            return (
              <Tab 
                key={index} 
                showClose={ value.close }
                title={value.title}
                style={{height : '80%'}}

              >
                <Grid container direction="row" justify="center">
                  <Grid item>
                    <br/>
                    { value.content }                   
                  </Grid>
                </Grid>
              </Tab>
            );
          })
        } 
      </Tabs>
    </div>
  )
}

const mapStateToProps = state => 
{
  console.log(state);
  return (
    {
      story : state.pilaroid.story,
      activeTab : state.pilaroid.storytab
    }
  )
}

const mapDispatchToProps = dispatch => ({
  setStory : (...value) => dispatch(setStory(...value)),
  addStoryPersonnage : (...value) => dispatch(addStoryPersonnage(...value)),
  deleteStoryPersonnage : (...value) => dispatch(deleteStoryPersonnage(...value)),
  setStoryPersonnage : (...value) => dispatch(setStoryPersonnage(...value)),  
  addStoryScene : (...value) => dispatch(addStoryScene(...value)),
  deleteStoryScene : (...value) => dispatch(deleteStoryScene(...value)), 
  deleteStoryScenePersonnage : (...value) => dispatch(deleteStoryScenePersonnage(...value)), 
  moveStoryScene : (...value) => dispatch(moveStoryScene(...value)),
  setStoryScene : (...value) => dispatch(setStoryScene(...value)),
  setStorySettings : (...value) => dispatch(setStorySettings(...value)),
  addStoryDialog : (...value) => dispatch(addStoryDialog(...value)),
  moveStoryDialog: (...value) => dispatch(moveStoryDialog(...value)),
  deleteStoryDialog : (...value) => dispatch(deleteStoryDialog(...value)),
  setStoryDialog : (...value) => dispatch(setStoryDialog(...value)),
  setStoryTab : (...value) => dispatch(setStoryTab(...value))
})

export default connect(mapStateToProps, mapDispatchToProps)(Story)
