/* eslint-disable react/prop-types */
import React from 'react';
import { Value } from './components'
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import PersonIcon from '@material-ui/icons/Person';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Tooltip from '@material-ui/core/Tooltip';

const useStyles = makeStyles((theme) => ({
  
  delete: {  
    cursor: "pointer"
  },  
  personns : {
    paddingTop : 5
  },

  table : {
    position: 'relative',
    zIndex : 9999
  }
}));

export default function NbPersonnages(props) {
  const classes = useStyles();

  const {text, value, onChange, onAdd, onDelete,colors, ...rest} = props;

  const enabled = value.length < 8;

  console.log('NbPersonnages value',value)

  const handleAdd = () => {        
    onAdd();
  };

  const handleDelete = () => (
    onDelete(value.length-1)
  );

  const handleChange = (index,key,value) => {
    console.log(key,value);
    onChange(index,key,value);
  };


  const valueComponent = <div className={classes.personns}> 
    {[...Array(value.length)].map((e, i) => {
      return <PersonIcon fontSize="large" key={i} style={colors[i]}/>
    })} 
    { value.length > 0 && <DeleteIcon onClick={handleDelete} className={classes.delete} color='action' />}
  </div> 

  const addicon = <Tooltip enterDelay={500} title="Ajouter un personnage"><IconButton disabled={!enabled}  onClick={handleAdd}  ><PersonAddIcon /></IconButton></Tooltip>

  const advancedComponent = value.length > 0 ?
    [<TableContainer component={Paper} className={classes.table}>
      <Table  aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell><PersonIcon fontSize="small"/></TableCell>
            <TableCell>Name</TableCell>
            <TableCell>Alias</TableCell>                
          </TableRow>
        </TableHead>
        <TableBody>
          {[...Array(value.length)].map((e, i) => (
            <TableRow key={i}>
              <TableCell component="th" scope="row">
                <PersonIcon fontSize="small" key={i} style={colors[i]}/>
              </TableCell>
              <TableCell align="right"><TextField id={i} value={value[i].name} onChange={(event) => (handleChange(i,'name',event.target.value))} /></TableCell>
              <TableCell align="right"><TextField id={i+20} value={value[i].alias} onChange={(event) => (handleChange(i,'alias',event.target.value))} /></TableCell>             
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>]
    :
    null

  return (
    <Value icon={addicon} text={text} value={valueComponent} advanced={advancedComponent}/>
  );
}
