const mapStateToProps = state => 
{
  console.log(state);
  return (
    {
      settings : state.pilaroid.settings
    }
  )
}

const mapDispatchToProps = dispatch => ({
  setSetting : (...value) => dispatch(setSetting(...value)),
  deleteSetting : (...value) => dispatch(deleteSetting(...value)),
})

export default connect(mapStateToProps, mapDispatchToProps)(Photo)

