import React, {useState} from 'react';
import { Value } from './components'
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';



const useStyles = makeStyles((theme) => ({
  button: {        
    maxWidth: '100%',
    paddingTop : 5
  } 
}));

function Action(props) {
  const classes = useStyles();

  const {icon, text,buttonText, onClick, ...rest} = props;

  
  const valueComponent = <div className={classes.button}><Button variant="outlined" size="small"  onClick={onClick}>{buttonText}</Button></div>
  
  
  return (
    <Value icon={icon} text={text} value={valueComponent} />
  );
}

export default Action;
