
const deepCopyFunction = (inObject) => {
  let outObject, value, key

  if (typeof inObject !== "object" || inObject === null) {
    return inObject // Return the value if inObject is not an object
  }

  // Create an array or object to hold the values
  outObject = Array.isArray(inObject) ? [] : {}

  for (key in inObject) {
    value = inObject[key]

    // Recursively (deep) copy for nested objects, including arrays
    outObject[key] = deepCopyFunction(value)
  }

  return outObject
}




const text = {
  text : '',
  font : null,
  size : null,
}

const scenes = {
  type : 2,
  settings : {},
  actors : [],
  dialogs : [],
  note :  deepCopyFunction(text),
  narrator :  deepCopyFunction(text),
  resetcamera : null,

}

const dialogs = {
  text : deepCopyFunction(text),
  actor : null,
}

const personnage = {
  name : null,
  alias : null
}

const synopsys = {
  title : deepCopyFunction(text),
  personnages : [],
  font : {font : null,size:null},
  icon : null,
  note : deepCopyFunction(text),
}

const defaut = {  
  settings: {type : 1},
  scene : deepCopyFunction(scenes),
  storytab : 0,
  story : {    
    type : 3,
    synopsys : deepCopyFunction(synopsys),
    scenes : [ deepCopyFunction(scenes)]
  },
  video : {
    type : 4,
    mode : null,
    fps : null
  }
}

defaut.qrcodeinstant = defaut.settings

const pilaroid = (state = defaut, action) => {
  switch (action.type) {        
    case 'SET_STORY_TAB' : {
      let rc = {...state};
      rc.storytab = action.value;
      return rc;
    }

    case 'SET_VIDEO_VALUE' : {      
      let rc = {...state};      
      rc.video[action.name] = action.value;
      console.log('SET VIDEO VALUE',action,rc);
      return rc;
    }
    
    case 'SET_SETTING_VALUE' : {
      console.log('Set settings',action)
      let rc = {...state};
      rc.settings[action.plugin] || (rc.settings[action.plugin] = {});
      rc.settings[action.plugin][action.name] = action.value
      console.log('Set settings after',rc.settings)
      return rc
    }
    case 'DELETE_SETTING_VALUE' : {
      let rc = {...state};
      console.log(action)
      rc.settings[action.plugin] && rc.settings[action.plugin][action.name] && delete rc.settings[action.plugin][action.name]
      return rc
    }
    case 'SET_STORY_VALUE' : {
      let rc = {...state};
      action.key ?
        rc.story.synopsys[action.name][action.key] = action.value
        :
        rc.story.synopsys[action.name] = action.value
      console.log('Set story value',rc)
      return rc
    }
    
    case 'ADD_STORY_PERSONNAGE' : {
      let rc = {...state};
      rc.story.synopsys.personnages.push(deepCopyFunction(personnage))      
      return rc      
    }

    case 'DELETE_STORY_PERSONNAGE' : {
      let rc = {...state};
      rc.story.scenes.map((scene,idx) => {
        let i = rc.story.synopsys.personnages.indexOf(action.personnageId)
        console.log('delete actors in scene',i, 'personnageIds',action.personnageId,scene)

        rc.story.scenes[idx].actors = rc.story.scenes[idx].actors.filter((actor) => (actor != action.personnageId)) ;
        rc.story.scenes[idx].dialogs = scene.dialogs.filter((dialog) => (dialog.actor != action.personnageId));
      }) 
      rc.story.synopsys.personnages.pop();
      return rc      
    }

    case 'SET_STORY_PERSONNAGE_VALUE' : {
      let rc = {...state};      
      rc.story.synopsys.personnages[action.personnageId][action.name] = action.value
      return rc
    }
    
    case 'ADD_STORY_SCENE' : {
      let rc = {...state};
      rc.story.scenes.push(deepCopyFunction(scenes))      
      return rc      
    }


    case 'DELETE_STORY_SCENE' : {
      let rc = {...state};
      rc.story.scenes.splice(action.sceneId, 1);
      return rc      
    }
    case 'MOVE_STORY_SCENE' : {
      let rc = {...state};
      let c = rc.story.scenes[action.sceneId1];
      rc.story.scenes[action.sceneId1] = rc.story.scenes[action.sceneId2];
      rc.story.scenes[action.sceneId2] = c;
      return rc      
    }

    case 'SET_STORY_SCENE_VALUE' : {
      console.log('SSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSS story scene value',action,state)
      let rc = {...state};
      if (action.key) {
        rc.story.scenes[action.sceneId][action.name][action.key] = action.value
      } else {    
        rc.story.scenes[action.sceneId][action.name] = action.value
      }
      console.log('SSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSSS story scene value',rc)

      return rc      
    }
    case 'SET_STORY_SCENE_SETTING' : {
      let rc = {...state};
      console.log('SET_STORY_SCENE_SETTING',rc.story.scenes,action)
      rc.story.scenes[action.sceneId].settings = deepCopyFunction(rc.settings)
      return rc
    }
    case 'SET_STORY_SCENE_DIALOG_VALUE' : {
      let rc = {...state};        
      if (action.key) {    
        rc.story.scenes[action.sceneId].dialogs[action.dialogId][action.name][action.key] = action.value
      } else {
        rc.story.scenes[action.sceneId].dialogs[action.dialogId][action.name] = action.value
      }
      return rc      
    }

    case 'ADD_STORY_SCENE_DIALOG' : {
      let rc = {...state};
      let dialog = deepCopyFunction(dialogs)
      dialog.actor = action.actorId
      rc.story.scenes[action.sceneId].dialogs.push(dialog)             
      return rc      
    }

    case 'MOVE_STORY_SCENE_DIALOG' : {
      console.log('MOVE_STORY_SCENE_DIALOG',action,state)
      let rc = {...state};      
      let c = rc.story.scenes[action.sceneId].dialogs[action.dialogId1];
      rc.story.scenes[action.sceneId].dialogs[action.dialogId1] = rc.story.scenes[action.sceneId].dialogs[action.dialogId2]
      rc.story.scenes[action.sceneId].dialogs[action.dialogId2] = c;
      console.log('MOVE_STORY_SCENE_DIALOG',action,rc)
      return rc      
    }

    case 'DELETE_STORY_SCENE_DIALOG' : {
      let rc = {...state};      
      rc.story.scenes[action.sceneId].dialogs.splice(action.dialogId, 1);      
      return rc      
    }

    case 'DELETE_STORY_SCENE_PERSONNAGE' : {
      let rc = {...state}
      rc.story.scenes[action.sceneId].actors.splice(rc.story.scenes[action.sceneId].actors.indexOf(action.personnageId), 1);
      rc.story.scenes[action.sceneId].dialogs = rc.story.scenes[action.sceneId].dialogs.filter((dialog) => (dialog.actor != action.personnageId));
      return rc
    }

    case 'SET_SCENE_VALUE' : {      
      let rc = {...state};
      if (action.key) {
        rc.scene[action.name][action.key] = action.value
      } else {    
        rc.scene[action.name] = action.value
      }      
      return rc      
    }
    case 'SET_SCENE_SETTING' : {
      let rc = {...state};
      rc.scene.settings = deepCopyFunction(rc.settings)
      return rc
    }
    case 'SET_SCENE_DIALOG_VALUE' : {
      let rc = {...state};        
      if (action.key) {    
        rc.scene.dialogs[action.dialogId][action.name][action.key] = action.value
      } else {
        rc.scene.dialogs[action.dialogId][action.name] = action.value
      }
      return rc      
    }
    case 'ADD_SCENE_DIALOG' : {
      let rc = {...state};
      let dialog = deepCopyFunction(dialogs)
      dialog.actor = action.actorId
      rc.scene.dialogs.push(dialog)             
      return rc      
    }

    case 'MOVE_SCENE_DIALOG' : {
      console.log('MOVE_STORY_SCENE_DIALOG',action,state)
      let rc = {...state};      
      let c = rc.scene.dialogs[action.dialogId1];
      rc.scene.dialogs[action.dialogId1] = rc.scene.dialogs[action.dialogId2]
      rc.scene.dialogs[action.dialogId2] = c;
      console.log('MOVE_STORY_SCENE_DIALOG',action,rc)
      return rc      
    }

    case 'DELETE_SCENE_DIALOG' : {
      let rc = {...state};      
      rc.scene.dialogs.splice(action.dialogId, 1);      
      return rc      
    }

    case 'DELETE_SCENE_PERSONNAGE' : {
      let rc = {...state}
      rc.scene.actors.splice(rc.scene.actors.indexOf(action.personnageId), 1);
      rc.scene.dialogs = rc.scene.dialogs.filter((dialog) => (dialog.actor != action.personnageId));
      return rc
    }
    
    default:
      return state
  }
}

export default pilaroid
