import React,{useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Box from '@material-ui/core/Box';

import InputLabel from '@material-ui/core/InputLabel';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import DeleteIcon from '@material-ui/icons/Delete';
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  text: {
    margin: 0,
  },

  value: {
    margin: 0,
    padding: 0,

  },
  advanced: {
    margin: 0,
    paddingLeft: 10,
  },
  advancedValue: {
    margin: 0,
  },
  label : {
    color: "gray"

  }
}));

export default function Value(props) {
  const classes = useStyles();

  const {icon, text, value, advanced, onDelete} = props;

  const [showAdvanced,setShowAdvanced] = useState(false)

  console.log('advanced = ',advanced,text)

  advanced && advanced.map((component,i) => {
    console.log(component,i)
  })

  //<InputLabel id={component.props.labelId}>{component.props.text}</InputLabel>

  return (
    <div className={classes.root}>  
      <Box display="flex" flexDirection="row">
        <Box width="50px" display="flex" xs={1} justifyContent="center" alignItems="center">
          {icon}
        </Box>
        <Box display="flex" flexDirection="row" flexWrap="wrap">          
          <Box display="flex" flexDirection="column" style={{marginRight : '10px'}}>
            {text && 
              <Box>                
                <InputLabel>{text} </InputLabel>
               
              </Box>
            }      
            <Box >
              {value}              
            </Box>              
          </Box>
          {(showAdvanced && advanced) &&  advanced.map((component,i) => (
            <Box style={{marginRight : '10px',maxHeight: '60px'}} display="flex" flexDirection="column">
              {text && 
                <Box>                
                  <InputLabel className={classes.label} id={component.props.labelId}>{component.props.text}</InputLabel>
                </Box>
              }
              <Box>                            
                {component}
              </Box>                
            </Box>
          ))}
        </Box>
        {onDelete && 
        <Box width="30px" display="flex" justifyContent="center" alignItems="center">          
            <DeleteIcon style={{cursor : 'pointer'}} onClick={() => onDelete()}/>             
        </Box>
        }
        {advanced && 
        <Box width="30px" display="flex" xs={1} justifyContent="center" alignItems="center">
          {showAdvanced ? 
            <ChevronLeftIcon style={{cursor : 'pointer'}} onClick={() => setShowAdvanced(false)}/> 
            : 
            <ChevronRightIcon style={{cursor : 'pointer'}} onClick={() => setShowAdvanced(true)}/>
          }
        </Box>
        }
      </Box>
    </div>
  );
}
