import React, {useState} from 'react';
import { Value } from './components'
import TextField from '@material-ui/core/TextField';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';
import Input from '@material-ui/core/Input';
import { default as UISLider } from '@material-ui/core/Slider';
import Box from '@material-ui/core/Box';
import Tooltip from '@material-ui/core/Tooltip';
import { useMediaQuery } from '@material-ui/core';


const useStyles = makeStyles((theme) => ({
  slider: {
    [theme.breakpoints.down('sm')]: {
      width : 260,      
    },   
    
    [theme.breakpoints.up('sm')]: {
      width : 320,
      marginRight : '10px'
    },   
    maxWidth: '100%' 
  },
  input :{    
    width : 60,    
  }, 
}));

export const fonts = {
  'Default' : null,
  'Arial' : 'arial.ttf',
  'Comics' : 'comics.ttf',
  'TypeWriter' : 'typewriter.ttf'
}

export const sizes = {
  'Default' : null,
  'Extra small' : 8,
  'Small' : 10,
  'Standard' : 12,
  'Large' : 14,
  'Extra large' : 16,
  'norme' : 20
}

export default function Slider(props) {
  const classes = useStyles();

  const {icon, text, value, onChange, onDelete, advanced, defaut, ...rest} = props;

  const handleSliderChange = (event, newValue) => {
    console.log('slider change',event,newValue)

    //setValue(newValue); 
    onChange(newValue);
  };

  const handleInputChange = (event,a,b) => {
    console.log('slider change',event,a,b)
    const newValue = event.target.value === '' ? '' : Number(event.target.value);
    //setValue(newValue);    
    onChange(newValue);
  };
  const theme = useTheme();  
  const isDesktop = useMediaQuery(theme.breakpoints.up('lg'), {
    defaultMatches: true
  });

  const valueComponent = 
  <Box display="flex" flexDirection="row">
    <Box>
      <UISLider
        {...rest}                        
        value={value ? value : defaut}
        onChange={handleSliderChange}
        aria-labelledby="input-slider"
        className={classes.slider} 
      />
    </Box>
    {isDesktop &&
      <Box>
        <Input
          className={classes.input}
          value={value ? value : defaut}
          margin="dense"
          onChange={handleInputChange}
          disableUnderline={true}
          inputProps={{
            step: props.step,
            min: props.min,
            max: props.max,
            type: 'number',
            'aria-labelledby': 'input-slider'
          }}        
        />
      </Box>
    }
  </Box>
  
  
  const advancedComponents = advanced ? [] : null; 

  const icone= <Tooltip enterDelay={500} title="Reset"><IconButton onClick={onDelete}>{icon}</IconButton></Tooltip>

  return (
    <Value icon={icone} text={text} value={valueComponent} advanced={advancedComponents}/>
  );
}
