import React, {useState} from 'react';
import { Value } from './components'
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';


const useStyles = makeStyles((theme) => ({
  text: {  
    [theme.breakpoints.down('sm')]: {
      width : 220,      
      maxWidth: '100%'
    },   
    
    [theme.breakpoints.up('sm')]: {
      width : 320,
      marginRight : '10px',
      maxWidth: '100%'
    },    
  },
  dialog : {
    [theme.breakpoints.down('sm')]: {
      width : 180,      
    },   
    
    [theme.breakpoints.up('sm')]: {
      width : 280,
      marginRight : '10px'
    },

  },
  font :{    
    width : 180, 
   
  },
  size :{    
    width : 100,

  }
 
}));

export const fonts = {
  'Default' : null,
  'Arial' : 'arial.ttf',
  'Comics' : 'comics.ttf',
  'TypeWriter' : 'typewriter.ttf'
}

export const sizes = {
  'Default' : null,
  'Extra small' : 8,
  'Small' : 10,
  'Standard' : 12,
  'Large' : 14,
  'Extra large' : 16,
  'norme' : 20
}

export default function Text(props) {
  const classes = useStyles();

  const {icon, text, value, onChange, onDelete, advanced, dialog, ...rest} = props;

  const handleChange = (event, newValue,c) => {
    console.log('Teext change',event, event.target.value)   
    onChange(event.target.value,'text');
  };

  const handleFontChange = (event, newValue,b) => {
    console.log(event,newValue.props.value,b)    
    onChange(newValue.props.value,'font');
  };

  const handleSizeChange = (event, newValue) => {    
    onChange(newValue.props.value,'size');
  };
  
  console.log('advanced text',advanced,value)

  const valueComponent = <TextField {...rest} className={dialog ? classes.dialog : classes.text} id="value" value={value.text} onChange={handleChange} />
  const advancedComponents = advanced ? [
    <Select
      labelId="font"
      text="Police"
      id="font"
      value={value.font}
      onChange={handleFontChange}
      className={classes.font}   
      displayEmpty   
    >      
      {Object.keys(fonts).map((key,i) => {
        return (<MenuItem key={i} value={fonts[key]}>{key}</MenuItem>)
      })}      
    </Select>,
    <Select
      labelId="size"
      text="Taille"
      id="size"
      value={value.size}
      onChange={handleSizeChange}
      className={classes.size}
      displayEmpty

  >      
    {Object.keys(sizes).map((key,i) => {
      return (<MenuItem key={i} value={sizes[key]}>{key}</MenuItem>)
    })}      
  </Select>]
    : null; 

  return (
    <Value icon={icon} text={text} value={valueComponent} advanced={advancedComponents} onDelete={onDelete}/>
  );
}
