import React, { useState } from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/styles';

import { useMediaQuery } from '@material-ui/core';

import { Sidebar, Topbar, Footer, Qrcode } from './components';
import { connect } from 'react-redux'
import { setSetting } from '../../actions'

const useStyles = makeStyles(theme => ({
  root: {
    paddingTop: 56,

    height: '100%',
    [theme.breakpoints.up('sm')]: {
      paddingTop: 64
    },
  },

  shiftContent: {
    paddingLeft: 240,
    paddingRight: 400

  },
  content: {
    height: '100%',
    overflow: 'auto'

  }
}));


const Main = props => {
  
  const { children, value, helpinstant,helpprint } = props;
  const { name } = children.props  
  const classes = useStyles();
  const theme = useTheme();
  const isDesktop = useMediaQuery(theme.breakpoints.up('lg'), {
    defaultMatches: true
  });

  const [openSidebar, setOpenSidebar] = useState(false);
  const [openQrcode, setOpenOpenqrcode] = useState(false);

  


  const handleSidebarOpen = () => {
    setOpenSidebar(true);
  };

  const handleSidebarClose = () => {
    setOpenSidebar(false);
  };

  const handleQrcodeOpen = () => {
    setOpenOpenqrcode(true);
  };

  const handleQrcodeClose = () => {
    console.log('close');
    setOpenOpenqrcode(false);
  };

  
  const shouldOpenSidebar = isDesktop ? true : openSidebar;
  const shouldOpenQrCode = isDesktop ? true : openQrcode;

  const values = (value && value['qrcode'] && value['qrcode'][name]) ? value['qrcode'][name] : {}

  console.log('main qr code instant',props.qrcodeinstant)
  
  return (
    <div
      key={name}
      className={clsx({
        [classes.root]: true,
        [classes.shiftContent]: isDesktop
      })}
    >
      <Topbar onSidebarOpen={handleSidebarOpen}  onQrcodeOpen={handleQrcodeOpen}/>
      <Sidebar
        onClose={handleSidebarClose}
        open={shouldOpenSidebar}
        variant={isDesktop ? 'persistent' : 'temporary'}
      />
      <main className={classes.content}>
        {React.cloneElement(children, { ...children.props,values:values, setSetting: (key,value) => props.setSetting(name,key,value)})}             
      </main>      
      <Qrcode
        key={1}
        isDesktop={isDesktop}
        onClose={handleQrcodeClose}
        open={shouldOpenQrCode}
        variant={isDesktop ? 'persistent' : 'temporary'}   
        qrcodeprint={props.qrcodeprint}     
        qrcodeinstant={props.qrcodeinstant}
        helpinstant={helpinstant} 
        helpprint={helpprint}    
      />
    </div>
  );
};

//<Footer/>

Main.propTypes = {
  children: PropTypes.node
};

const mapStateToProps = state => ({
  value : state.pilaroid
})

const mapDispatchToProps = dispatch => ({
  setSetting : (name,key,value) => dispatch(setSetting(name,key,value))
})

export default connect(mapStateToProps, mapDispatchToProps)(Main)
