/* eslint-disable react/no-multi-comp */
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import NotesIcon from '@material-ui/icons/Notes';
import FeaturedVideoIcon from '@material-ui/icons/FeaturedVideo';
import { Text, SelectPersonnages, Dialogues, Boolean } from '../../../components'
import Tooltip from '@material-ui/core/Tooltip';
import AutorenewIcon from '@material-ui/icons/Autorenew';
import IconButton from '@material-ui/core/IconButton';



const useStyles = makeStyles((theme) => ({
  root: {
  },
  
}));



export default function Scene(props) {

  const {value, personnages, colors, setScene, deleteScenePersonnage,setDialog, addDialog, deleteDialog, moveDialog } = props

  const classes = useStyles();  

  console.log(value)


  return (
    <div className={classes.root}>
      <SelectPersonnages
        key='selectpersonnage'
        onChange={(value) => setScene('actors',value)}
        onDelete={deleteScenePersonnage}
        value={value.actors}
        personnages={personnages}
        text="Sélectionner les personnages"
        colors = {colors}        
      />
      <br/>      
      <Dialogues
        onChange={(dialogId,value,key) => setDialog(dialogId,'text',value,key)}
        value={value.dialogs}
        personnages={personnages}
        SelectPersonnages={value.actors}
        onAdd={addDialog}
        onDelete={deleteDialog}
        onMove={moveDialog}
        onMovePersonnage={(value) => setScene('actors',value)}
        onDelete={deleteDialog}
        text="Ajouter des répliques"
        colors = {colors}                
      />
      <br/>
      <Text
        advanced
        multiline
        onChange={(value) => setScene('narrator',value,'text')}
        text="Narrateur"
        value={value.narrator}
        icon={          
          <Tooltip enterDelay={500} title="Reset">
            <IconButton onClick={() => setScene('narrator',"",'text')}>
              <FeaturedVideoIcon/>
            </IconButton>
          </Tooltip>}                
      />
      <br/>
      <Text
        multiline
        onChange={(value) => setScene('note',value,'text')}
        text="Note"
        value={value.note}
        icon={
          <Tooltip enterDelay={500} title="Reset">
            <IconButton onClick={() => setScene('note',"",'text')}>
              <NotesIcon/>
            </IconButton>
          </Tooltip>
        }
        
      />     
    </div>
  )
}
