import React, {useState} from 'react';
import { Value } from './components'
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import {default as UISelect} from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';
import Input from '@material-ui/core/Input';
import { default as UISLider } from '@material-ui/core/Slider';
import Box from '@material-ui/core/Box';
import Tooltip from '@material-ui/core/Tooltip';


const useStyles = makeStyles((theme) => ({
  select: { 
    [theme.breakpoints.down('sm')]: {
      width : 260,      
    },       
    [theme.breakpoints.up('sm')]: {
      width : 320,
    },   
    maxWidth: '100%'  
  } 
}));

function Select(props) {
  const classes = useStyles();

  const {icon, text, value, onChange, options, defaut, ...rest} = props;

  const handleChange = (event, newValue,b) => {
    console.log(event,newValue.props.value,b)    
    onChange(newValue.props.value);
  };
  
  const valueComponent =
  <UISelect    
    text={text}
    value={value ? value : defaut}
    onChange={handleChange}
    className={classes.select}   
    displayEmpty   >      
    
    {Object.keys(options).map((key,i) => {
      return (<MenuItem key={i} value={options[key]}>{key}</MenuItem>)
    })}      
  </UISelect>
  
  
  return (
    <Value icon={icon} text={text} value={valueComponent} />
  );
}

export default Select;
