import React from 'react';
import { Route } from 'react-router-dom';
import PropTypes from 'prop-types';

const RouteWithLayout = props => {
  const { layout: Layout, component: Component, name, qrcodeinstant, qrcodeprint,helpinstant,helpprint, ...rest } = props;  

  console.log('RouteWithLayout qr code instant',props.qrcodeinstant)


  return (
    <Route
      {...rest}
      render={matchProps => (
        <Layout qrcodeinstant={props.qrcodeinstant} qrcodeprint={props.qrcodeprint} helpinstant={helpinstant} helpprint={helpprint} >
          <Component {...{...matchProps,name : name, }} />
        </Layout>
      )}
    />
  );
};

RouteWithLayout.propTypes = {
  component: PropTypes.any.isRequired,
  layout: PropTypes.any.isRequired,
  name : PropTypes.string,
  path: PropTypes.string
};

export default RouteWithLayout;
