import React, {useState} from 'react';
import { Value } from './components'
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import IconButton from '@material-ui/core/IconButton';
import { fonts, sizes} from './Text'

const useStyles = makeStyles((theme) => ({
  font :{    
    width : 180, 
   
  },
  size :{    
    width : 100,

  }
 
}));


export default function Font(props) {
  const classes = useStyles();

  const {icon, text, value, onChange, advanced, ...rest} = props;

  const handleFontChange = (event, newValue,b) => {
    console.log(event,newValue.props.value,b)    
    onChange(newValue.props.value,'font');
  };

  const handleSizeChange = (event, newValue) => {    
    onChange(newValue.props.value,'size');
  };
  
  console.log('advanced text',advanced,value)

  const valueComponent = <Select
    className={classes.font}
    displayEmpty
    id="font"
    onChange={handleFontChange}
    text="Police"   
    value={value.font}   
  >      
    {Object.keys(fonts).map((key,i) => {
      return (<MenuItem key={i} value={fonts[key]}>{key}</MenuItem>)
    })}      
  </Select>
  
  const advancedComponents = advanced ? [    
    <Select
      className={classes.size}
      displayEmpty
      id="size"
      onChange={handleSizeChange}
      text="Taille"
      value={value.size}

    >      
      {Object.keys(sizes).map((key,i) => {
        return (<MenuItem key={i} value={sizes[key]}>{key}</MenuItem>)
      })}      
    </Select>]
    : null; 

  return (
    <Value advanced={advancedComponents} icon={icon} text={text} value={valueComponent}/>
  );
}
