import React, {useState} from 'react';
import { Value } from './components'
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import Switch from '@material-ui/core/Switch';

const useStyles = makeStyles((theme) => ({
  text: {    
    width : '60%',
    maxWidth : 600
  }, 
}));


export default function Boolean(props) {
  const classes = useStyles();

  const {icon, text, value, onChange, onDelete, defaut, advanced, ...rest} = props;

  const handleChange = (event, newValue,c) => {
    console.log('BOOLEAN change',event, newValue,c)   
    onChange(newValue);
  };

  console.log("Boolean value",value)

  
  const valueComponent = <Switch {...rest} id="value" checked={(value===null || value===undefined) ? defaut : value} onChange={handleChange} />
  const advancedComponents = null
      
  return (
    <Value icon={icon} text={text} value={valueComponent} advanced={advancedComponents} onDelete={onDelete}/>
  );
}
