/* eslint-disable react/prop-types */
import React from 'react';
import { Value } from './components'
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import PersonIcon from '@material-ui/icons/Person';
import PersonOutlineIcon from '@material-ui/icons/PersonOutline';
import IconButton from '@material-ui/core/IconButton';
import GroupAddIcon from '@material-ui/icons/GroupAdd';
import Tooltip from '@material-ui/core/Tooltip';


const useStyles = makeStyles((theme) => ({
  
  delete: {  
    cursor: "pointer"
  },  
  personns : {
    paddingTop : 5
  },

  table : {
  }
}));

export default function SelectPersonnages(props) {
  const classes = useStyles();

  const {text, value, personnages, onChange,colors, onDelete} = props;

  console.log('SelectPersonnages props',props)



  const handleAdd = (i) => {
    if (value.length < 4) {
      let rc = value;
      value.push(i);  
      onChange(rc);    
    }
    
  };

  const handleDelete = (i) => {
    onDelete(i)
  };

  
  const valueComponent = <div className={classes.personns}> 
    {[...Array(personnages.length)].map((e, i) => (
      value.includes(i) ?
        <Tooltip enterDelay={500} title="Retirer ce personnage de la scêne">
          <PersonIcon fontSize="large" key={i} style={{ ...colors[i],cursor:'pointer'}} onClick={() => handleDelete(i)}/>
        </Tooltip>      
        :
        <Tooltip enterDelay={500}  title={value.length < 4 ? "Ajouter ce personnage a la scêne" : "Maximum 4 personnes par scene"}>
          <PersonOutlineIcon fontSize="large" key={i} style={{ ...colors[i],cursor:'pointer'}} onClick={() => handleAdd(i)}/>
        </Tooltip>
    ))}

  </div> 

  const addicon = <GroupAddIcon />

  return (
    <Value icon={addicon} text={text} value={valueComponent}/>
  );
}
