import React, { useState } from 'react';
import { makeStyles } from '@material-ui/styles';
import { Slider, Select } from 'components';
import PhotoSizeSelectLargeIcon from '@material-ui/icons/PhotoSizeSelectLarge';
import Typography from '@material-ui/core/Typography';
import { Divider, Drawer } from '@material-ui/core';
import { connect } from 'react-redux'
import { setVideo } from 'actions'
import DynamicFeedIcon from '@material-ui/icons/DynamicFeed';
import PartyModeIcon from '@material-ui/icons/PartyMode';
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles(theme => ({
  root: {
    padding: theme.spacing(4)
  }
}));

const Video = (props) => {
  const classes = useStyles();
  const { value, defaut } = props;

  const marks = [
    {
      value: 14,
      label: 'default',
    }
  ];



  return (
    <div className={classes.root}>  
      <Grid container   direction="row" justify="center">
        <Grid item>
          <Select
            icon={<PartyModeIcon/>}
            text='Video mode'
            options={{'Centre' : 'center','Gauche' : 'left', 'Live' : 'live','Long exposure' : 'exposure'}}
            value={value.mode}
            defaut={'center'}
            onChange={value => props.setVideo('mode',value)}
            onDelete={value => props.setVideo('mode',null)}
          />
          <br/>
          <Slider
            aria-labelledby="non-linear-slider"
            icon={<DynamicFeedIcon/>}
            max={32}
            min={0}        
            step={1}
            text="FPS"
            name="fps"
            value={value.fps}
            defaut={14}
            valueLabelDisplay="auto"
            marks={marks}
            track={false}
            onChange={value => props.setVideo('fps',value)}
            onDelete={value => props.setVideo('fps',null)}
          />
        </Grid> 
      </Grid> 
    </div>

  );
};

const mapStateToProps = state => 
{  
  console.log('State roman',state,state.scene)
  return ({value : state.pilaroid.video})
}


const mapDispatchToProps = dispatch => ({
  setVideo : (...value) => dispatch(setVideo(...value)),
})

export default connect(mapStateToProps, mapDispatchToProps)(Video)
