import ListItemText from '@material-ui/core/ListItemText';

var QRCode = require('qrcode.react');


const compress = (inObject) => {
  let outObject, value, key

  let keys = {
    'settings' : 1,
    'scene' : 2,
    'story' : 3,
    'title' : 4,
    'personnages' : 5,
    'actors' : 5,
    'font' : 6,
    'size' : 7,
    'icon' : 8,
    'note' : 9,
    'scenes' : 10,
    'video' : 11,
    'fps' : 12,
    'text' : 13,
    'dialogs' : 14,
    'narrator'  : 15,
    'resetcamera' : 16,
    'actor' : 17,
    'alias' : 18,
    'width' : 19,
    'camera' : 20,
    'printer' : 22,
    'global' : 23,
    'type' : 24,
    'brightness' : 30,
    'contrast' : 32,
    'historize' : 33,
    'iso' : 34,
    'saturation' : 35,
    'sharpness' : 36,    
    'dots' : 37,
    'heattime' : 38, 
  }

  if (typeof inObject !== 'object' || inObject === null) {
    return inObject // Return the value if inObject is not an object
  }

  // Create an array or object to hold the values
  outObject = Array.isArray(inObject) ? [] : {}

  for (key in inObject) {
    // Recursively (deep) copy for nested objects, including arrays
    value = compress(inObject[key])
    if (value !== null) { 
      if (Object.keys(keys).includes(key)) {
        key = keys[key]
      }

      outObject[key] = value
    }
  }

  return outObject
}

