const Qrcode = props => {
  const { open, variant, onClose, className, isDesktop, qrcodeinstant, qrcodeprint,helpinstant,helpprint, ...rest } = props;

  const classes = useStyles();

  const maxLength = 624;

  const [qrcodemode,setQrcodeMode] = useState(1);

  const value = qrcodemode == 1 ? compress(qrcodeinstant) :  compress(qrcodeprint);
  const jsonvalue = JSON.stringify(value);
  const nbmessages = Math.ceil(jsonvalue.length / maxLength);
  const datalength = jsonvalue.length / nbmessages
  const lastdatalength = jsonvalue.length / nbmessages + (jsonvalue.length % nbmessages)
  
  const getQrCodeValue = () => {
    
    let qrcodes = []
    let start = 0
    for (var i = 0; i < nbmessages; i++) {
      const page=`#${i}#${nbmessages}#`
      const printmode = qrcodemode;      
      let length = i == nbmessages-1 ? lastdatalength : datalength;      
      const data = jsonvalue.substring(start, start+length);
      start = start + length;
      qrcodes.push(`${page}${printmode}${data}`);  
    }
    return(qrcodes)
  }

  var QrCodeValues = getQrCodeValue();

  console.log('QrCodesValues',QrCodeValues)

  const [currentPage, setCurrentPage] = useState(0);
  const [isActive, setIsActive] = useState(true);
  
  function toggle() {
    setIsActive(!isActive);
  }

  useEffect(() => {
    console.log('****************************UseEffect',isActive,currentPage)
    let interval = null;
    if (isActive) {
      
      interval = setInterval(() => {                
        console.log('****************************UseEffect Interval',isActive,currentPage)
        setCurrentPage(currentPage + 1);        
      }, 2000);
    } else if (!isActive) {
      clearInterval(interval);
    }
    return () => clearInterval(interval);
  }, [isActive, currentPage]);

  const i = currentPage % nbmessages;

  const helpone = ["Placer l'appareil à 30 cm de l'écran","Attendre que l'appareil détecte le QrCode","L'appareil vibrera deux fois lorsqu'il l'a detecté"]
  const helpmulti = ["Placer l'appareil à 30 cm de l'écran","Attendre que l'appareil détecte les QrCodes","L'appareil vibrera une fois lorqu'il detecte un Qrcode","L'appareil vibrera deux fois lorsqu'il a détecté tous les Qrcodes"]
  
  
  const helpmode = qrcodemode == 1 ? helpinstant : helpprint;
  const helpqrcode = nbmessages > 1 ? helpmulti : helpone;
  const help = [].concat(helpqrcode).concat(helpmode)
  console.log(help)

  return (
    <Drawer
      anchor="right"
      classes={{ paper: classes.drawer }}
      onClose={onClose}
      open={open}
      variant={variant}
    >
      <div
        {...rest}
        className={clsx(classes.root, className)}
      >
        <Box display="flex" flexDirection="column" height="100%">
            
          <Box display="flex" xs={12}  flexDirection="column" justifyContent="center">
            <Box justifyContent="center" width="100%" height="100%" maxWidth="calc(height)" display="flex">
              <QRCode 
                className={classes.qrcode}
                onClick={onClose}
                size={400}
                value={QrCodeValues[i]}
                renderAs={"canvas"}
              />
            </Box>
            {nbmessages > 1 &&
            <Box justifyContent="center" display="flex">                        
              {i+1}/{nbmessages}
            </Box>
          }
          </Box>  
                  
          <Box  display="flex" className={classes.pagination}>
            <Select onChange={setQrcodeMode} value={qrcodemode} icon={<CloseIcon/>} text='Qrcode Mode' options={{'Utiliser Directement':1,'Imprimer pour plus tard':2}}/>
          </Box>                      
        <Box display="flex" xs={12}  overflow='auto'>            
          <List className={classes.help}>
            {help.map((value,i) =>  <ListItem ><ListItemText primary={value}/></ListItem>)}
          </List>
        </Box>
        </Box>

      </div>
    </Drawer>
  );
};

Qrcode.propTypes = {
  className: PropTypes.string,
  isDesktop: PropTypes.bool,
  onClose: PropTypes.func,
  open: PropTypes.bool.isRequired,
  value: PropTypes.object,
  variant: PropTypes.string.isRequired
};

export default Qrcode
