import React from 'react';
import ReactDOM from 'react-dom';
import ReactDragListView from 'react-drag-listview/lib/index.js';
import { Value } from './components'
import TextField from '@material-ui/core/TextField';
import { makeStyles } from '@material-ui/core/styles';
import PersonIcon from '@material-ui/icons/Person';
import PersonOutlineIcon from '@material-ui/icons/PersonOutline';
import IconButton from '@material-ui/core/IconButton';
import Text from'./Text';
import ChatBubbleIcon from '@material-ui/icons/ChatBubble';
import Tooltip from '@material-ui/core/Tooltip';


const useStyles = makeStyles((theme) => ({
  
  delete: {  
    cursor: "pointer"
  },  
  liste : {
    listStyleType: "none",    
  },

  lilist : {
    float : 'left',
    textDecoration: null
  },

  liste2 : {
    listStyleType: "none",
    paddingTop : 10,    
  },


  lilist2 : {   
    textDecoration: null,
    padding : 5,
    paddingLeft : 35

  }
}));


export default function Dialogues(props) {  

  const classes = useStyles();

  const {text, value, personnages, SelectPersonnages, onAdd, onDelete,onMove,onChange,onMovePersonnage,colors} = props;

  const that = this;

  const handleAdd = (i) => {
    if (value.length < 6) {
      onAdd(i)
    }
  };

  const handleDelete = (i) => {
    let rc = value
    rc.splice(rc.indexOf(i), 1);    
    onChange(rc);
  };
  
  const dragProps = {
    onDragEnd(i, j) {      
      let data = [...SelectPersonnages];
      let c = data[i];
      data[i] = data[j];
      data[j] = c;
      console.log("yooooooooooooooooooooooooooooooooooooooooooooooooooooooo",SelectPersonnages,data)
      onMovePersonnage(data)
    },
    nodeSelector: 'li',
    handleSelector: 'a'
  };

  const dragProps2 = {
    onDragEnd(i, j) {      
      onMove(i, j)
    },
    nodeSelector: 'li',
    handleSelector: 'a'
  };

  const valueComponent = 
    <ReactDragListView {...dragProps}>
      <ul className={classes.liste}>
        {SelectPersonnages.map((item, index) => (   
          <li className={classes.lilist} key={index}>                  
            <a href="#">        
              <Tooltip enterDelay={500}  title={value.length < 6 ? "Ajouter un phylactère. Glisser pour changer l'ordre des personnages" : "Max 6 phylactères, Glisser pour changer l'ordre des personnages"} >
                <PersonIcon  style={colors[item]} fontSize="large" onClick={() => handleAdd(item)} />
              </Tooltip>
            </a>
          </li>                             
        ))}
      </ul>
    </ReactDragListView>     
 
  return (     
    <div>
      <Value icon={<ChatBubbleIcon/>} text={text} value={valueComponent}/>
      <div className={classes.dialogs} >
        <ReactDragListView {...dragProps2}>
          <ul className={classes.liste}>
            {value.map((item, index) => (   
              <li className={classes.lilist2} key={index}>                  
                <a href="#"><Text dialog multiline  icon={<PersonIcon  style={colors[item.actor]}/>} value={item.text} advanced onChange={(value,key) => onChange(index,value,key)} onDelete={() => onDelete(index)}/> </a>
              </li>                             
            ))}
          </ul>
        </ReactDragListView>  
      </div>
    </div> 
  );
}


