import React from 'react';
import { makeStyles } from '@material-ui/styles';
import { Action } from 'components';
import PhotoSizeSelectLargeIcon from '@material-ui/icons/PhotoSizeSelectLarge';
import Typography from '@material-ui/core/Typography';
import { Divider, Drawer } from '@material-ui/core';
import Switch from '@material-ui/core/Switch';
import { connect } from 'react-redux'
import { setScene, setSceneSettings, deleteScenePersonnage, addSceneDialog, moveSceneDialog, deleteSceneDialog, setSceneDialog} from '../../actions'

import { Scene } from './component'
import { colors } from './Story'
import Grid from '@material-ui/core/Grid';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
    width: '100%',
    height: '100%',
    padding: theme.spacing(4),
    
  },
}));

const Roman = (props) => {
  const classes = useStyles();

  //props.setQrCodeInstantValue(props.scene)

  console.log('Remoan Value',props.scene)

  return (
    <div className={classes.root}>
      <Grid container   direction="row" justify="center">
        <Grid item>
          <Scene
            addDialog={props.addSceneDialog}
            deleteDialog={props.deleteSceneDialog}
            deleteScenePersonnage={props.deleteScenePersonnage}
            moveDialog={props.moveSceneDialog}
            personnages={[{name:null,alias:null},{name:null,alias:null},{name:null,alias:null},{name:null,alias:null}]}
            setDialog={props.setSceneDialog}
            setScene={props.setScene}
            value={props.scene}
            colors={colors}
          />
          <br/>
          <Action
            onClick = {props.setSceneSettings}
            text = "Importer les paramètres photo actuel"
            icon={<PhotoSizeSelectLargeIcon/>}
            buttonText = "importer"
          />
        </Grid>
      </Grid>     
    </div>

  );
};

const mapStateToProps = state => 
{  
  console.log('State roman',state,state.scene)
  return ({scene : state.pilaroid.scene})
}


const mapDispatchToProps = dispatch => ({
  setScene : (...value) => dispatch(setScene(...value)),
  deleteScenePersonnage : (...value) => dispatch(deleteScenePersonnage(...value)), 
  addSceneDialog : (...value) => dispatch(addSceneDialog(...value)),
  moveSceneDialog : (...value) => dispatch(moveSceneDialog(...value)),
  deleteSceneDialog : (...value) => dispatch(deleteSceneDialog(...value)),
  setSceneDialog : (...value) => dispatch(setSceneDialog(...value)),
  setSceneSettings : (...value) => dispatch(setSceneSettings(...value)),
})

export default connect(mapStateToProps, mapDispatchToProps)(Roman)

